import React from "react";
import img from "./../../assessts/meca.webp";
import style from "./ProjectsComponentStyle.module.css";
const Meca = () => {
  return (
    <>
      <div className={style.imgwrapper}>
        <img src={img} alt="" />
      </div>
      <div className={style.body}>
        <div>
          <h2>task</h2>
          <p>
            Develop the front end of the site and improving registration and
            engagement through email.
          </p>
        </div>
        <br />
        <div>
          <h2>solution</h2>
          <p>
            Developed by adding subscription features and the participating
            companies dashboard was activated.
          </p>
        </div>
        <br />
        <div>
          <h2>tools</h2>
          <ul className={style.tools}>
            <li>html</li>
            <li>css</li>
            <li>javascript</li>
            <li>php</li>
            <li>my sql</li>
          </ul>
        </div>
        <br />
      </div>
    </>
  );
};

export default Meca;
