import React from "react";
import { useParams, useNavigate } from "react-router-dom";
import { coursesData } from "../../db/courses";
import Header from "../../Components/Header/Header";
import SingleCourseStyle from "./singleCourse.module.css";
import { Helmet } from "react-helmet-async";
const SingleCourse = () => {
  const navigate = useNavigate();
  const { courseId } = useParams();
  const course = coursesData.find((course) => course.id === courseId);
  const { title, body } = course;

  return (
    <>
    <Helmet>
      <title>Courses - {title}</title>
    </Helmet>
      <Header>{title}</Header>
      <section className={SingleCourseStyle.maincontent}>
        {body}
        <div className={SingleCourseStyle.btns}>
          <button onClick={() => navigate("/courses")}>Back</button>
        </div>
      </section>
    </>
  );
};

export default SingleCourse;
