import React from "react";
import img from "./../../assessts/science.png";
import style from "./ProjectsComponentStyle.module.css";
const ScienceE = () => {
  return (
    <>
      <div className={style.imgwrapper}>
        <img src={img} alt="" />
      </div>
      <div className={style.body}>
        <div>
          <h2>task</h2>
          <p>
            Transform the traditional course into interactive e-learning content which
            it make the student can simulate difficult-to-experiment experiments
            in the lab.,
          </p>
        </div>
        <br />
        <div>
          <h2>solution</h2>
          <p>
            Through using JS code,can we make it easy to adding this feature to
            all Authoring tools.
          </p>
        </div>
        <br />
        <div>
          <h2>tools</h2>
          <ul className={style.tools}>
            <li>articlate storyline 360</li>
            <li>javascript</li>
          </ul>
        </div>
        <br />
      </div>
    </>
  );
};

export default ScienceE;
