import React, { useState } from "react";
import ReactPlayer from "react-player/lazy";
import showCourseStyle from "./showCourse.module.css";
const JavaScript = () => {
  const [jsCourseData] = useState([
    {
      id: "1",
      title: "two sum",
      url: "3f37BFzO7uw",
    },
    {
      id: "2",
      title: "plaindrome number",
      url: "X4MjRVNNAw4",
    },
  ]);
  const [videoId, setVideoId] = useState("3f37BFzO7uw");

  // Navigation
  const [currentIndex, setIndex] = useState(0);
  const n = () => {
    if (currentIndex === jsCourseData.length - 1) {
      setIndex(0);
    } else {
      setIndex((prev) => prev + 1);
    }
  };
  const b = () => {
    if (currentIndex === 0) {
      setIndex(jsCourseData.length - 1);
    } else {
      setIndex((prev) => prev - 1);
    }
  };

  return (
    <>
      <div className={showCourseStyle.carsoule}>
        <span onClick={() => b()}></span>
        <h3 onClick={() => setVideoId(jsCourseData[currentIndex].url)}>
          {jsCourseData[currentIndex].title}
        </h3>
        <span onClick={() => n()}></span>
      </div>
      <div className={showCourseStyle.player}>
        <ReactPlayer
          width="100%"
          height="100%"
          style={{ margin: "auto" }}
          controls={true}
          url={`https://www.youtube.com/watch?v=${videoId}`}
        />
      </div>
    </>
  );
};

export default JavaScript;
