import React from "react";
import img from "./../../assessts/splash.webp";
import style from "./ProjectsComponentStyle.module.css";
const Splash = () => {
  return (
    <>
      <div className={style.imgwrapper}>
        <img src={img} alt="" />
      </div>
      <div className={style.body}>
        <div>
          <h2>task</h2>
          <p>Creating a website to provide graphic design courses.</p>
        </div>
        <br />
        <div>
          <h2>solution</h2>
          <p>
            - Create the site and deliver the courses through recorded videos.{" "}
            <br />- At the end of each course there's a final exam to improve
            the skills that have been acquired through the course.
          </p>
        </div>
        <br />
        <div>
          <h2>tools</h2>
          <ul className={style.tools}>
            <li>html</li>
            <li>css</li>
            <li>javascript</li>
            <li>adobe photoshop</li>
            <li>adobe illustrator</li>
          </ul>
        </div>
        <br />
      </div>
    </>
  );
};

export default Splash;
