import React from "react";
import img from "./../../assessts/privateksa.webp";
import style from "./ProjectsComponentStyle.module.css";
const Ptksa = () => {
  return (
    <>
      <div className={style.imgwrapper}>
        <img src={img} alt="" />
      </div>
      <div className={style.body}>
        <div>
          <h2>task</h2>
          <p>
            Need to connect the teachers at KSA with a platform which they can
            manage thier private sessions.
          </p>
        </div>
        <br />
        <div>
          <h2>solution</h2>
          <p>
            The website was developed as front end with modern technologies to
            make it easy to use.
          </p>
        </div>
        <br />
        <div>
          <h2>tools</h2>
          <ul className={style.tools}>
            <li>html</li>
            <li>css</li>
            <li>javascript</li>
          </ul>
        </div>
        <br />
      </div>
    </>
  );
};

export default Ptksa;
