import React from "react";
import { useParams, useNavigate } from "react-router-dom";
import { ProjectsData } from "../../db/projects";
import Header from "../../Components/Header/Header";
import singleProjectStyle from "./singleProject.module.css";
import { Helmet } from "react-helmet-async";
const SingleProject = () => {
  const navigate = useNavigate();
  const { projectId } = useParams();
  const project = ProjectsData.find((project) => project.id === projectId);
  const { component, title, url } = project;
  return (
    <>
      <Helmet>
        <title>Projects - {title}</title>
      </Helmet>
      <Header>{title}</Header>
      <section className={singleProjectStyle.maincontent}>
        {component}
        <div className={singleProjectStyle.btns}>
          <button onClick={() => navigate("/projects")}>Back</button>
          <button style={{ backgroundColor: "#001d3d" }}>
            <a
              href={url}
              target="_blank"
              rel="noreferrer"
              style={{ color: "#ffffffcc" }}
            >
              Demo
            </a>
          </button>
        </div>
      </section>
    </>
  );
};

export default SingleProject;
