import React from "react";
import img from "./../../assessts/soft-skill-cover.jpg";
import style from "./ProjectsComponentStyle.module.css";
const SoftSkillsE = () => {
  return (
    <>
      <div className={style.imgwrapper}>
        <img src={img} alt="" />
      </div>
      <div className={style.body}>
        <div>
          <h2>tasks</h2>
          <p>
            <ol>
              <li>
                Search about the Topic and determine the initial Learning
                objectives and outlines.
              </li>
              <li>
                Kick-off meeting with SME to determine the final Outlines.
              </li>
              <li> Reviewing script.</li>
              <li> Writing clear storboard for Media Team.</li>
              <li>
                Developing the final project and make sure it met with Learning
                objectives.
              </li>
            </ol>
          </p>
        </div>
        <br />
        <div>
          <h2>solution</h2>
          <p>
            Work with the team to get the final project using the authoring
            tools
          </p>
        </div>
        <br />
        <div>
          <h2>tools</h2>
          <ul className={style.tools}>
            <li>articlate storyline 360</li>
            <li>adobe photoshop</li>
          </ul>
        </div>
        <br />
      </div>
    </>
  );
};

export default SoftSkillsE;
