import React from "react";
import blogsStyle from "./blogs.module.css";
import { blogsData } from "./../../db/blogs";
import Header from "../../Components/Header/Header";
import { Link } from "react-router-dom";
import { Helmet } from "react-helmet-async";
const Blogs = () => {
  return (
    <>
    <Helmet>
      <title>Blogs</title>
    </Helmet>
      <Header>blogs</Header>
      <section className={blogsStyle.mainContent}>
        {blogsData.map(({ id, img, title }) => {
          return (
            <div className={blogsStyle.blogsCard} key={id}>
              <img src={img} alt="" />
              <Link to={`/blogs/${id}`}>
                <h3>
                  {title} <span></span>
                </h3>
              </Link>
            </div>
          );
        })}
      </section>
    </>
  );
};

export default Blogs;
