import React from "react";
import img from "./../../assessts/solar_system.jpg";
import style from "./ProjectsComponentStyle.module.css";
const SolarSystemE = () => {
  return (
    <>
      <div className={style.imgwrapper}>
        <img src={img} alt="" />
      </div>
      <div className={style.body}>
        <div>
          <h2>problem</h2>
          <p>
            I was told by a teacher that he had a hard a problem with some of
            his students to understand the solar system so he asked me to
            develop a material to make it easy to keep information in their
            brains.
          </p>
        </div>
        <br />
        <div>
          <h2>solution</h2>
          <p>
            After analyzing the students properties and what they need I have
            developed an interactive game that make the student more focused and
            enjoying get information about the solar system with his new friend
            UFO which it the main character in the game.
          </p>
        </div>
        <br />
        <div>
          <h2>tools</h2>
          <ul className={style.tools}>
            <li>articlate storyline 360</li>
            <li>javascript</li>
            <li>adobe illustrator</li>
            <li>adobe photoshop</li>
          </ul>
        </div>
        <br />
      </div>
    </>
  );
};

export default SolarSystemE;
