import React from "react";
import { useParams, useNavigate } from "react-router-dom";
import { blogsData } from "./../../db/blogs";
import Header from "../../Components/Header/Header";
import singleBlogStyle from "./singleBlog.module.css";
import { Helmet } from "react-helmet-async";
const SingleBlog = () => {
  const navigate = useNavigate();
  const { blogId } = useParams();
  const blog = blogsData.find((blog) => blog.id === blogId);
  const { title, component } = blog;
  return (
    <>
    <Helmet>
      <title>Blogs - {title}</title>
    </Helmet>
      <Header>{title}</Header>
      <section className={singleBlogStyle.maincontent}>
        {component}
        <div className={singleBlogStyle.btns}>
          <button onClick={() => navigate("/blogs")}>Back</button>
        </div>
      </section>
    </>
  );
};

export default SingleBlog;
