import React from "react";
import coursesStyle from "./courses.module.css";
import { coursesData } from "../../db/courses";
import Header from "../../Components/Header/Header";
import { Link } from "react-router-dom";
import { Helmet } from "react-helmet-async";

const Courses = () => {
  return (
    <>
      <Helmet>
        <title>Courses</title>
      </Helmet>
      <Header>courses</Header>
      <section className={coursesStyle.mainContent}>
        {coursesData.map(({ id, title, img }) => {
          return (
            <div className={coursesStyle.courseCard} key={id}>
              <img src={img} alt="" />
              <Link to={`/courses/${id}`}>
                <h3>
                  {title} <span></span>
                </h3>
              </Link>
            </div>
          );
        })}
      </section>
    </>
  );
};

export default Courses;
