import React from "react";
import { NavLink } from "react-router-dom";
import style from "./nav.module.css";
import homeImg from "./../../assessts/navbar_home.png";
import projectsImg from "./../../assessts/navbar_projects.png";
import coursesImg from "./../../assessts/navbar_courses.png";
import blogsImg from "./../../assessts/navbar_blogs.png";
import contactImg from "./../../assessts/navbar_contact.png";
const Nav = () => {
  const isActive = (props) => {
    return {
      backgroundColor: props.isActive && "rgba(255, 255, 255, 0.7)",
      color: props.isActive && "#658ec6",
    };
  };

  return (
    <nav className={style.nav}>
      <ul>
        <li>
          <NavLink style={isActive} to="/" end>
            <img src={homeImg} alt="" />
          </NavLink>
        </li>
        <li>
          <NavLink style={isActive} to="projects">
            <img src={projectsImg} alt="" />
          </NavLink>
        </li>
        <li>
          <NavLink style={isActive} to="courses">
            <img src={coursesImg} alt="" />
          </NavLink>
        </li>
        <li>
          <NavLink style={isActive} to="blogs">
            <img src={blogsImg} alt="" />
          </NavLink>
        </li>
        <li>
          <NavLink style={isActive} to="contact">
            <img src={contactImg} alt="" />
          </NavLink>
        </li>
      </ul>
    </nav>
  );
};

export default Nav;
