import React from "react";
import img from "./../../assessts/behance.png";
import style from "./ProjectsComponentStyle.module.css";
const Behance = () => {
  return (
    <>
      <div className={style.imgwrapper}>
        <img src={img} alt="" />
      </div>
      <div className={style.body}>
        <div>
          <h2>task</h2>
          <p>
            Taking freelance work from different agencies and produce a
            custom-made design.
          </p>
        </div>
        <br />
        <div>
          <h2>approach</h2>
          <strong
            style={{ fontSize: "1rem", color: "#023e8a", lineHeight: "1.7" }}
          >
            The final design is produced based on ....
          </strong>
          <ul style={{ fontSize: "1rem", color: "#023e8a", lineHeight: "1.7" }}>
            <li>Identity.</li>
            <li>Color Pallet Selection.</li>
            <li>Know the customer's requirements and product quality.</li>
          </ul>
        </div>
        <br />
        <div>
          <h2>tools</h2>
          <ul className={style.tools}>
            <li>adobe photoshop</li>
            <li>adobe illustrator</li>
          </ul>
        </div>
        <br />
      </div>
    </>
  );
};

export default Behance;
